.relatedproducts{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90dvh;
}
.relatedproducts h1{
    color: hsl(0, 0%, 9%); 
    font-size: 50px;
    font-weight: 600;
}
.relatedproducts hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: hsl(0, 0%, 15%);
}
.relatedproducts-item{
    margin-top: 50px;
    display: flex;
    gap: 30px;
}