.newsletter{
    width: 65%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(
        180deg,
        hsl(296, 100%, 94%) 0%,
        hsla(138, 100%, 94%, 0.133) 60%
    );
    gap: 30px;
}

.newsletter h1{
    color: hsl(0, 0%, 27%);
    font-size: 55px;
    font-weight: 600;
}

.newsletter p{
    color: hsl(0, 0%, 27%);
    font-size: 20px;
}

.newsletter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: hsl(0, 0%, 100%);
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border: 1px solid hsl(0, 0%, 89%);
}
.newsletter input{
    width: 500px;
    margin-left: 30px;
    border: none;
    outline: none;
    color: hsl(0, 0%, 38%);
    font-family: poppins;
    font-size: 16px;
}
.newsletter div button{
    width: 210px;
    height: 70px;
    border-radius: 80px;
    background: hsl(0, 0%, 0%);
    color: hsl(0, 0%, 100%);
    font-size: 16px;
    cursor: pointer;
}