.navbar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: hsl(0, 0%, 9%);
    font-size: 38px;
    font-weight: 600;
}
.nav-logo p:hover{
    color:hsl(0, 0%, 4%);
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: hsl(0, 0%, 38%);
    font-size: 20px;
    font-weight: 500;
}

.nav-menu li{
    display: flex;
    flex-direction: column; /*for drop down menu*/
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: hsl(0, 100%, 63%);
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid hsl(0, 0%, 48%);
    border-radius: 75px;
    color: hsl(0, 0%, 32%);
    font-size: 20px;
    font-weight: 500;
    background: hsl(0, 0%, 100%);
    cursor: pointer;
}
.nav-login-cart button:active{
    color: hsl(0, 0%, 95%);
}
.nav-login-cart button:hover{
    background: hsl(0, 0%, 95%);
}
.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: hsl(0, 100%, 50%);
    color: hsl(0, 0%, 100%);
}